import React, {useCallback, useEffect, useRef, useState} from "react";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import axios from "axios";
import {useLocation, useNavigate} from "react-router-dom";
import Quill from "quill";

// Editor 에 장착할 modules 정보
const modules = {
    toolbar: {
        container: [
            [{ font: [] }],
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline"],
            [{ color: [] }, { background: [] }], // dropdown with defaults from theme
            [{ script: "sub" }, { script: "super" }], // superscript/subscript
            [{ list: "ordered" }, { list: "bullet" }],
            [{ indent: "-1" }, { indent: "+1" }], // outdent/indent
            [{ direction: "rtl" }], // text direction
            ["link", "image"],
            ["clean"], // remove formatting button
        ],
        handlers: {
            image: imageHandler, // 이미지 선택 됐을때 실행됨
        },
        ImageResize : {
            parchment : Quill.import('parchment')
        },
    },
};

// 사용자가 이미지 추가를 했을때 작동되는 handler
function imageHandler() {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();

    input.onchange = () => {
        const file = input.files[0];
        // console.log("사용자가 입력한 image :", file);

        const formData = new FormData();
        formData.append("image", file);
        // console.log("Server 로 쏘기 위한 image : ", formData);

        const res =
            axios.post("/upload/image/register", formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                },
            })
            .then((response) => {
                // console.log("Server 로부터 받은 Image URL :", response.data);
                const range = this.quill.getSelection();
                this.quill.insertEmbed(range.index, "image", `http://localhost:3000${response.data.imageUrl}`);
                // Front 에서 즉시 이미지 띄우기 위한 경로 지정
                // ex) http://localhost:3000/freeBoard/12.png 같은 형식으로 가져와 Editor 에 즉시 이미지를 볼 수 있게 처리함
            })
            .catch((error) => {
                console.error("Error uploading image:", error.response.data);
            });
    };
}

const Editor = ({updateBtn}) => {
    const [title, setTitle] = useState("");     // 제목
    const [content, setContent] = useState(""); // 내용(텍스트 + 이미지)
    const [moveUrl, setMoveUrl] = useState(false);
    const titleRef = useRef(null);
    const navigate = useNavigate();
    const locate = useLocation().state; // 이전 page 에서 전달받은 parameter

    // 제목 handler
    const titleOnChangeHandler = useCallback((e)=> {
        setTitle(e.target.value);
    });

    // 수정 버튼 클릭 시 해당 게시글의 정보 가져옴
    const getFreeBoardContents = async () =>{
        const res = await axios.get("/freeBoard/view", {
            params : {
                freeBoarNo : locate.state
            }
        })
            .then((response => showContents(title, content)))
            .catch((error) => console.log("error occurred!"));
    }

    const showContents = (dbTitle, dbContent) =>{
        if(dbTitle && dbContent){
            setTitle(dbTitle);
            setContent(dbContent);
        }else{
            alert("가져올 정보가 없습니다.");
        }
    }

    useEffect(() => {
        if(updateBtn){

        }
    }, []);

    // 이미지 파일 생성, DB 에 data 입력 성공 시 자유게시판 목록 페이지로 이동
    useEffect(() => {
        // console.log("URL 정보 : ", moveUrl);
        if(moveUrl){
            navigate("/freeboard/list");
        }
    }, [moveUrl]);

    // TODO
    // 게시글 작성 시 회원 아이디를 로그인된 계정 정보로 변경

    // 작성 완료 button
    const goAdd = async () => {
        if(title){
            if(content){
                const memberId = "갠역시21"; // 로그인 기능 미구현으로 임시 회원 데이터 삽입
                const data = {freeTitle : title, freeContent : content, memberId:memberId}
                const res = await axios.post("/freeBoard/register", data)
                .then((response => navigate("/community/freeBoard/view", {
                    state:response.data
                })
                ))
                    .catch((error) => console.log(error));
            }else{
                alert("내용을 입력하세요");
            }
        }else{
            alert("제목을 입력하세요");
            titleRef.current.focus();
        }
    }

    return (
        <div>
            <input type="text" ref={titleRef} onChange={titleOnChangeHandler} className="text-black text-4xl w-full" placeholder="제목을 입력하세요" />
            <hr className="my-3" />
            <ReactQuill
                value={content}
                onChange={setContent}
                modules={modules}
                theme="snow"
                style={{
                    height:'500px',
                    color:'#000000',
                    overflowY:'visible',
                }}
            />
            <div className="text-end mt-14">
                <button onClick={goAdd} className="bg-blue-500 text-100 p-2 font-bold text-white rounded hover:bg-blue-700">작성 완료</button>
            </div>
        </div>
    );
};

export default Editor;
